import React from 'react'

import { useFormik } from 'formik';

import { TextField } from '@mui/material';

const RouterModal = () => {
    
    const validate = values => {
        const errors = {};

        if (!values.userName) errors.userName = ' ';
        if (!values.userNumber) errors.userNumber = ' ';

        if (!values.userTrouble) errors.userTrouble = ' ';

        return errors;
    }

    const formik = useFormik({
        initialValues: {
            userName: '',
            userNumber: '',
            userTrouble: ''
        },
        validate,
        onSubmit: values => {
            alert(JSON.stringify(values, null, 2))
        }
    })

    return (
        <div>
            <div className="modal fade" id="routerModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                <div className="modal-dialog" role="document">
                    <div className="modal-content">
                        <div className="modal-header">
                            <h5 className="modal-title" id="exampleModalLabel"> </h5>
                            <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
                        <div className="modal-body custom-modal-body">
                            <form onSubmit={ formik.handleSubmit }>
                            <TextField variant='standard' label=' *' name='userName' onChange={ formik.handleChange } value={ formik.values.userName }></TextField>
                            { formik.errors.userName ? <div className='modal-error-text'>{ formik.errors.userName }</div> : null }
                            <TextField variant='standard' label=' *' name='userNumber' onChange={ formik.handleChange } value={ formik.values.userNumber }></TextField>
                            { formik.errors.userNumber ? <div className='modal-error-text'>{ formik.errors.userNumber }</div> : null }
                            <TextField variant='standard' label=' ' name='userTrouble' onChange={ formik.handleChange } value={ formik.values.userEmail }></TextField>
                            { formik.errors.userTrouble ? <div className='modal-error-text'>{ formik.errors.userTrouble }</div> : null }
                            <button type="submit" className="btn btn-primary"></button>
                            <h6>       .</h6>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default RouterModal;
